import React, { useEffect, useRef } from "react";

import about02 from "/src/images/picture/about02.jpg"
import instagram from "../../images/icon/instagram.png"
import x from "../../images/icon/x.png"


const Component = ({ aboutUs, businessContents }) => {
  const imgRefs = useRef([]);

  useEffect(() => {
    const imgs = imgRefs.current;

    const update = () => {
      const wh = window.innerHeight;
      imgs.forEach((img, i) => {
        const rect = img.getBoundingClientRect();
        const visible = Math.min(rect.bottom, window.innerHeight) - Math.max(rect.top, 0);
        const ratio = Math.max(0, Math.min(1, visible / rect.height));
        const p = ratio;

        //  i === 12
        const from = i === 1
          ? [[20, 0], [100, 20], [80, 100], [0, 80]] //  
          : [[0, 20], [80, 0], [100, 80], [20, 100]];

        const to = i === 1
          ? [[0, 20], [80, 0], [100, 80], [20, 100]] //   from 
          : [[20, 0], [100, 20], [80, 100], [0, 80]];

        img.style.clipPath = `polygon(${from.map((f, j) =>
          `${f[0] + (to[j][0] - f[0]) * p}% ${f[1] + (to[j][1] - f[1]) * p}%`
        ).join(",")})`;

        const scale = 0.8 + (0.2 * p); //  70%  90%
        img.style.transform = `scale(${scale})`;
      });
    };

    window.addEventListener("scroll", update);
    window.addEventListener("resize", update);
    update();

    return () => {
      window.removeEventListener("scroll", update);
      window.removeEventListener("resize", update);
    };
  }, []);


  return (
    <section className="aboutSec">
      <div className="aboutBox">
        <h2 className="headLine">VISION</h2>
        <div ref={el => imgRefs.current[0] = el} className="photoBox">
          <img src={aboutUs.media.url} alt={aboutUs.title} />
        </div>
        <p className="txt" dangerouslySetInnerHTML={{ __html: aboutUs.content }} />
      </div>

      <div className="aboutBox">
        <h2 className="headLine">MISSION</h2>
        <div ref={el => imgRefs.current[1] = el} className="photoBox">
          <img src={about02} alt={aboutUs.title} />
        </div>
        <p className="txt">
          <br />
          <br />
          <br />
          <br />
          <br />
          PM<br />
          
        </p>
      </div>

      <div className="aboutBox">
        <h2 className="headLine">VALUE</h2>
        <div className="information left bottom fadeInUp">
          <h3>  <span>-A-TEAM</span></h3>
          <a href="https://www.instagram.com/sakurait/" target="_blank"><img src={instagram} alt="instagram" />sakurait</a>
        </div>
        <div ref={el => imgRefs.current[2] = el} className="photoBox">
          <img src={businessContents.media.url} alt={businessContents.title} />
        </div>
        <p className="txt" dangerouslySetInnerHTML={{ __html: businessContents.content }} />
      </div>

      <div className="concept">
        <div className="content">
          <div className="conceptTxt">
            <h2 className="headLine fadeInUp">CONCEPT</h2>
            <p className="fadeInUp">
              <br />
              <br />
              <br />
              
            </p>
          </div>
        </div>
      </div>
      <div className="content">
        <div className="information fadeInUp">
          <h3> <span>-12 WBO<br /></span></h3>
          <a href="https://www.instagram.com/_fujitakenji_/" target="_blank"><img src={instagram} alt="instagram" />_fujitakenji_</a>
          <a href="https://x.com/kenjixfujita" target="_blank"><img src={x} alt="x" />kenjixfujita</a>
        </div>
      </div>
    </section>
  );
};

export default Component;
